import chalk from "chalk"

import { prepareUrls } from "./prepare-urls"
import { IProgram } from "../internal"

export function printInstructions(program: IProgram): void {
  const urls = prepareUrls(
    program.https ? `https` : `http`,
    program.host,
    program.port
  )

  const appName = program.sitePackageJson.name || `(Unnamed package)`

  console.log(`\n${appName} is ready:`)

  if (urls.lanUrlForTerminal) {
    console.log(
      `  ${chalk.bold(`Local:`)}            ${
        urls.localUrlForTerminal
      }__graphql`
    )
    console.log(
      `  ${chalk.bold(`On Your Network:`)}  ${
        urls.lanUrlForTerminal
      }__graphql\n`
    )
  } else {
    console.log(`  ${urls.localUrlForTerminal}__graphql\n`)
  }
}
